<template>
  <b-card v-bind:title="item.title" class="mb-2">
    <b-row>
      <b-col md="6" class="preview">
        <div
          :id="imageId"
          :data-chemwriter-data="item.data.file"
          data-chemwriter-ui="image"/>
      </b-col>
      <b-col md="6">
        <b-container fluid>
          <b-row>
            Created at: {{ item.creation_date }}
          </b-row>
          <b-row>
            Author: {{ item.author_name }}
          </b-row>
          <b-row class="text-left">
            Description: <br />
            {{ item.description }}
          </b-row>
        </b-container>
      </b-col>
    </b-row>
  </b-card>
</template>
<script>
export default {
  name: 'item-preview',
  props: ['item'],
  computed: {
    imageId: {
      get() {
        return `image-${this.item.id}`;
      },
    },
  },
  updated() {
    const image = window.chemwriter.components[this.imageId];
    image.setMolfile(this.item.data.file);
  },
};
</script>

<style>
  .preview {
    height: 18em;
  }
</style>
